<template>
    <div class="procedure_set_rule">
        <div class="c_rule_header">
            <CContentHeader :isShowIcon="true" content="流程控制" />
            <el-button class="c_rule_header_btn_one" @click="back">上一步</el-button>
            <el-button class="c_rule_header_btn_two" @click="preserveBtn" type="primary">保存</el-button>
        </div>
        <div class="c_sule_settings">
            <div class="c_approval_box">
                <div class="c_approval_content">
                    <div class="c_visible_range">
                        <div class="c_visible_range_title">
                            可见范围
                        </div>
                        <div class="c_visible_range_range">
                            <div v-for="item in visibleRangeList" :key="item.depid" class="c_visible_range_range_branch">
                                <img v-if="item.type === 'dep' " src="../../assets/img/procedure/部门白.png" alt="">
                                <img v-else src="../../assets/img/procedure/人白.png" alt="">
                                <span>{{item.name}}</span>
                            </div>
                            <div v-if="visibleRangeList.length > 0" class="c_visible_range_range_btn" @click="reviseScope">
                                修改
                            </div>
                            <div v-else @click="reviseScope" class="c_condition_btn">添加</div>
                        </div>
                    </div>
                    <div class="c_approval_process">
                        <div class="c_approval_process_title">
                            审批流程
                        </div>
                        <div class="c_approval_process_box">
                            <div class="c_title">
                                审批人：
                            </div>
                            <div class="c_approval_by_list">
                                <div class="flex" v-for="(item,index) in approverPeopleList" :key="index">
                                    <div class="c_approval_by_name">
                                        <img v-if="item.avatar" :src="item.avatar" alt="">
                                        <img v-else :src="require('../../assets/img/procedure/staff.png')" alt="">
                                        <p>{{item.name}}</p>
                                    </div>
                                    <div class="c_approval_by_icon">
                                        <i class="el-icon-more"></i>
                                    </div>
                                </div>
                                <div class="c_approval_by_add" @click="addApproverPeople">
                                    <i class="el-icon-plus"></i>
                                </div>
                            </div>
                        </div>
                        <div class="c_approval_process_box">
                            <div class="c_title">
                                抄送人：
                            </div>
                            <div class="c_approval_by_list">
                                <div class="flex" v-for="(item,index) in copierPeopleList" :key="index">
                                    <div class="c_approval_by_name">
                                        <img v-if="item.avatar" :src="item.avatar" alt="">
                                        <img v-else :src="require('../../assets/img/procedure/staff.png')" alt="">
                                        <p>{{item.name}}</p>
                                    </div>
                                    <div class="c_approval_by_icon">
                                        <i class="el-icon-more"></i>
                                    </div>
                                </div>
                                <div class="c_approval_by_add" @click="addCopierPeople">
                                    <i class="el-icon-plus"></i>
                                </div>
                            </div>
                        </div>
                        <div class="c_condition_box">
                            <div class="c_title">
                                条件审批流程：
                            </div>
                            <div class="c_condition">
                                <!-- <div @click="isShow = !isShow" class="c_condition_btn">
                            添加
                            {path:'/procedureApprovalCondition', query:{ m_id:m_id}}
                            :to="{path:'/procedureApprovalCondition', query:{ m_id:m_id,r_id:r_id}}"
                            :to="'/procedureApprovalCondition/'+m_id"
                        </div> -->
                                <div @click="addBtn" class="c_condition_btn">添加</div>
                                <p>不同的条件，使用不同的审批流程</p>
                            </div>
                        </div>
                        <!-- 添加条件 -->
                        <div class="c_condition_add_box" v-for="(item,index) in ruleList" :key="index">
                            <div class="c_condition_add_header">
                                <div class="c_condition_add_title">
                                    {{item.title}}
                                </div>
                                <div @click="editBtn(index)" class="c_condition_add_edit">
                                    <i class="el-icon-edit"></i>
                                    编辑
                                </div>
                                <div @click="deleteBtn(item._id,index)" class="c_condition_add_delete">
                                    <i class="el-icon-delete"></i>
                                </div>
                            </div>
                            <div class="c_approval_process_box">
                                <div class="c_title no_pt">
                                    同时满足
                                </div>
                                <div class="c_approval_by_list">
                                    <span>申请人：</span>
                                    <span class="span-font" v-for="ruleItem in item.rule" :key="ruleItem.data"> {{ruleItem.name}}</span>
                                </div>
                            </div>
                            <div class="c_approval_process_box">
                                <div class="c_title">
                                    审批人
                                </div>
                                <div class="c_approval_by_list" v-for="(flowItem,flowIndex) in item.flow" :key="flowIndex">
                                    <div class="c_approval_by_name">
                                        <!-- <img :src="flowItem.avatar" alt=""> -->
                                        <!-- <img :src="require('../../assets/img/procedure/组56.png')" alt=""> -->
                                        <img v-if="flowItem.avatar" :src="flowItem.avatar" alt="">
                                        <img v-else :src="require('../../assets/img/procedure/staff.png')" alt="">
                                        <p>{{flowItem.name}}</p>
                                    </div>
                                    <div class="c_approval_by_icon" v-if="flowIndex !== (item.flow.length - 1)">
                                        <i class="el-icon-more"></i>
                                    </div>
                                </div>

                            </div>
                            <div class="c_approval_process_box">
                                <div class="c_title no_pt">
                                    抄送人
                                </div>
                                <div class="c_approval_by_list f12">
                                    <span v-for="(copyItem,copyIndex) in item.copy" :key="copyIndex">{{copyItem.name}}</span>
                                    <!-- <span>ZXQ</span> -->
                                </div>
                            </div>
                        </div>

                        <div class="c_revoke_box">
                            <div class="c_title">
                                撤销权限：
                            </div>
                            <div class="c_condition">
                                <div class="c_condition_radio">
                                    <!-- <el-radio v-model="radio" label="4">通过后允许撤销</el-radio> -->
                                    <el-checkbox v-model="revokePower">通过后允许撤销</el-checkbox>
                                </div>
                                <p>审批通过后，经审批人同意，可撤销。</p>
                            </div>
                        </div>

                        <div class="c_revoke_box no_flex">
                            <div class="c_title">
                                抄送通知：
                            </div>
                            <div>
                                <div class="c_condition_radio">
                                    <el-radio v-model="copierNotice" label="1">提交申请时抄送</el-radio>
                                </div>
                                <div class="c_condition_radio">
                                    <el-radio v-model="copierNotice" label="2">审批通过后抄送</el-radio>
                                </div>
                                <div class="c_condition_radio">
                                    <el-radio v-model="copierNotice" label="3">提交申请时和审批通过后度抄送</el-radio>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- 遮罩层组件 -->
        <div class="c_elastic_frame">
            <!-- <MaskLayer title="可见范围修改" :menuList="menuList" :dialogShow.sync="dialogShow" @cancel="cancelBtn"></MaskLayer> -->
            <MaskLayer v-if="dialogShow" :title="title" :twoTitle="twoTitle" :isOne="isOne" :isUser="isUser" :dialogShow.sync="dialogShow" :powerList="powerList" @submit="submitBtn" @reset="resetBtn"></MaskLayer>
        </div>
    </div>
</template>

<script>
    //引入遮罩层
    import MaskLayer from "../../components/c_maskLayer.vue"

    export default {
        data() {
            return {
                isShow: true, //是否显示添加内容框
                revokePower: false, //撤销权限 通过审批后，经审批人同意，可撤销申 请
                copierNotice: '1', //抄送通知 1表示提交申请时抄送 2表示审批通过后 抄送 3表示提交申请时和审批通过后都抄送

                title: '',
                twoTitle: '',

                dialogShow: false,
                powerList: [], //传给子组件的数组
                m_id: '', //模板id
                isRule: false, //是否修改可见范围
                isFlow: false, //是否修改审批人
                isCopy: false, //是否修改抄送人 
                isOne: false, //是否只选择一个
                isUser: false, //是否只选择人

                visibleRangeList: [], //可见范围
                approverPeopleList: [], //审批人 
                copierPeopleList: [], //抄送人

                ruleList: [], //流程规则列表
                ruleListData: [], //上传的参数
                userid: 'zxp17', //用户id
                isDel: true
            }
        },
        mounted() {
            console.log('我在吗');
            this.scrollBar();
            let ruleSettingsData = this.$zero.getSessionStorage('ruleSettingsData');

            if (ruleSettingsData) {
                this.visibleRangeList = ruleSettingsData.visibleRangeList; //可见范围
                this.approverPeopleList = ruleSettingsData.approverPeopleList; //审批人 
                this.copierPeopleList = ruleSettingsData.copierPeopleList; //抄送人
                this.revokePower = ruleSettingsData.revokePower; //撤销权限 通过审批后，经审批人同意，可撤销申 请
                this.copierNotice = ruleSettingsData.copierNotice; //抄送通知
                this.ruleList = ruleSettingsData.ruleList; //流程规则列表
                this.ruleListData = ruleSettingsData.ruleListData; //上传的参数
            }
            
            return;
        },
        methods: {
            reviseScope() { //可见范围修改按钮点击事件
                this.title = '可见范围';
                this.twoTitle = '以下部门或员工可见';

                this.dialogShow = true; //是否弹出遮罩层
                this.isRule = true;
                this.isOne = true;
                this.isUser = false;
                this.isFlow = false;
                this.isCopy = false;
                console.log(111);
                this.powerList = JSON.parse(JSON.stringify(this.visibleRangeList))

            },
            addApproverPeople() { //添加审批人
                this.title = '审批人';
                this.twoTitle = '以下为审批人';

                this.dialogShow = true; //是否弹出遮罩层
                this.isRule = false;
                this.isUser = true;
                this.isFlow = true;
                this.isCopy = false;
                console.log(111);
                this.powerList = JSON.parse(JSON.stringify(this.approverPeopleList))
            },
            addCopierPeople() { //添加抄送人
                this.title = '抄送人';
                this.twoTitle = '以下为抄送人';

                this.dialogShow = true; //是否弹出遮罩层
                this.isRule = false;
                this.isUser = true;
                this.isFlow = false;
                this.isCopy = true;
                console.log(111);
                this.powerList = JSON.parse(JSON.stringify(this.copierPeopleList))
            },
            submitBtn(item) { //遮罩层提交按钮
                this.dialogShow = false;
                console.log(item);
                let list = item.powerList
                if (this.isRule) {
                    this.visibleRangeList = JSON.parse(JSON.stringify(list))
                } else if (this.isFlow) {
                    this.approverPeopleList = JSON.parse(JSON.stringify(list))
                } else if (this.isCopy) {
                    this.copierPeopleList = JSON.parse(JSON.stringify(list))
                }
                this.scrollBar();

            },
            resetBtn() { //遮罩层取消按钮
                this.dialogShow = false;
            },
            back() {
                this.isDel = false;
                window.location.href = "/procedureSetTemplate";
                // window.history.go(-1);
                this.setSessionStorageData();

            },
            setSessionStorageData(index) {
                let ruleSettingsData = {
                    visibleRangeList: this.visibleRangeList,
                    approverPeopleList: this.approverPeopleList,
                    copierPeopleList: this.copierPeopleList,
                    revokePower: this.revokePower,
                    copierNotice: this.copierNotice,
                    ruleList: this.ruleList,
                    ruleListData: this.ruleListData
                }
                if (typeof index == 'number') {
                    ruleSettingsData.index = index;
                } else if (typeof ruleSettingsData.index == 'number') {
                    delete ruleSettingsData.index;
                }

                this.$zero.setSessionStorage({
                    ruleSettingsData
                })
            },
            async preserveBtn() { //保存按钮
                // let m_id = '123';
                console.log('保存了');
                if (this.visibleRangeList.length < 1) {
                    return this.$zero.ToastWarning('请选择可见范围')
                } else if (this.approverPeopleList.length < 1) {
                    return this.$zero.ToastWarning('请选择审批人')
                } 
                // else if (this.ruleList.length < 1) {
                //     return this.$zero.ToastWarning('请添加条件审批流程')
                // }

                this.addTemplate();
                // this.addRuleTemplate('5d50ddc33c185469045e2d4c');
                // this.setTemplate('5d50ddc33c185469045e2d4c');
            },
            async addTemplate() { //创建流程模板

                let ruleTemplate = this.$zero.getSessionStorage('ruleTemplate');
                console.log(ruleTemplate);

                // return
                let res = await this.$api.process.addTemplate({
                    name: ruleTemplate.name,
                    company_id: ruleTemplate.company_id,
                    list: ruleTemplate.list,
                    userid: ruleTemplate.userid,
                    icon: ruleTemplate.icon
                })

                console.log(res);
                if (res.statusCode === 72506) {
                    return this.$zero.ToastWarning('模板名称重复，请返回上一步修改模板名称')
                }else if(res.statusCode !== 75200) {
                    return this.$zero.ToastWarning(res.msg)
                }
                let m_id = res._id
                this.addRuleTemplate(m_id);
                this.setTemplate(m_id)

                // return res._id
            },
            async setTemplate(m_id) { //流程设置
                let visibleRange = []; //
                let copierPeople = '';
                let approverPeople = '';

                for (let i = 0; i < this.visibleRangeList.length; i++) {
                    visibleRange[i] = {
                        type: this.visibleRangeList[i].type,
                        data: this.visibleRangeList[i].depid,
                        name: this.visibleRangeList[i].name
                    }

                }
                for (let i = 0; i < this.approverPeopleList.length; i++) {
                    // approverPeople.push(this.approverPeopleList[i].depid)
                    if (i === this.approverPeopleList.length - 1) {
                        approverPeople += this.approverPeopleList[i].depid
                    } else {
                        approverPeople += this.approverPeopleList[i].depid + ','
                    }
                }
                for (let i = 0; i < this.copierPeopleList.length; i++) {
                    if (i === this.copierPeopleList.length - 1) {
                        copierPeople += this.copierPeopleList[i].depid
                    } else {
                        copierPeople += this.copierPeopleList[i].depid + ','
                    }

                }
                console.log(visibleRange);
                console.log(copierPeople);
                console.log(approverPeople);

                let res = await this.$api.process.setTemplate({
                    m_id,
                    visibleRange,
                    copierPeople,
                    approverPeople,
                    revokePower: this.revokePower,
                    copierNotice: this.copierNotice
                })
                console.log(res);


                if (res.statusCode === 75200) {
                    this.isDel = true;
                    this.$zero.ToastSuccess('设置成功');
                    // return;

                    this.$zero.removeSessionStorage('ruleSettingsData')
                    this.$zero.removeSessionStorage('ruleTemplate')
                    // return;
                    // rety
                    setTimeout(() => {
                        window.location.href = "/procedure";
                    }, 500)
                } else {
                    return this.$zero.ToastWarning(res.msg)
                }
            },
            async addRuleTemplate(m_id) { //添加流程规则
                console.log(this.ruleListData);
                var self = this;
                for (let i = 0; i < this.ruleListData.length; i++) {
                    (function() {
                        self.$api.process.addRuleTemplate({
                            m_id,
                            title: self.ruleListData[i].title,
                            rule: self.ruleListData[i].rule,
                            flow: self.ruleListData[i].flow,
                            copy: self.ruleListData[i].copy
                        }).then((res) => {
                            console.log(res);

                        }).catch((err) => {
                            console.log(err);

                        })

                    })()

                }

            },
            // url传递参数的另外一种方式
            // routeJump() { //点击编辑跳转页面
            // console.log(111);

            //     this.$router.push({
            //         name: 'procedureApprovalCondition',
            //         params: {
            //             id: '5d400c83f914115508e2bfd1'
            //         }
            //     })
            // }
            addBtn() { //添加按钮，添加流程规则设置
                this.isDel = false;
                this.setSessionStorageData();
                // this.$zero.removeSessionStorage('ruleList')
                // this.$router.push({
                //     path: '/procedureApprovalCondition'
                // })
                window.location.href = "/procedureApprovalCondition";
            },
            editBtn(index) { //编辑按钮，编辑流程规则设置
                // this.$zero.setSessionStorage({ //本地存储
                //     ruleList: this.ruleList[index]
                // })
                this.isDel = false;
                this.setSessionStorageData(index);
                // this.$router.push({
                //     path: '/procedureApprovalCondition',
                //     query: {
                //         m_id: this.m_id
                //     }
                // })
                window.location.href = "/procedureApprovalCondition";
            },
            async deleteBtn(id, index) { //删除流程规则设置
                console.log(id, index);
                this.ruleList.splice(index, 1)
                this.ruleListData.splice(index, 1)
                return;
                let res = await this.$api.process.delRuleTemplate({
                    m_id: this.m_id,
                    r_id: id
                })

                console.log(res);
                if (res.statusCode === 75200) {
                    this.$zero.ToastSuccess('删除成功')
                    this.ruleList.splice(index, 1)
                    this.scrollBar();
                    return;
                } else {
                    return this.$zero.ToastWarning(res.msg)
                }


            },
            async getRuleTemplate() { //获取流程规则设置
                // console.log();
                // return;

                let res = await this.$api.process.getRuleTemplate({
                    m_id: this.m_id,
                    userid: this.userid
                })
                console.log(res);
                if (res.statusCode === 75200) {
                    this.ruleList = res.result.ruleList
                } else {
                    return this.$zero.ToastWarning(res.msg)
                }

            },
            scrollBar() { //滚动条样式
            // console.log('滚动条样式');
            
                // $(".c_approval_box").getNiceScroll().remove();
                /**
                 * 设置滚动条滚动区域
                 * $('.el-table__body-wrapper')  需要滚动条的盒子
                 * niceScroll(".el-table__body",{})  需要滚动的内容盒子---滚动条盒子的子元素 {} 修改滚动条的参数详情百度搜索niceScroll
                 * cursorcolor    滚动条颜色
                 * cursorwidth    滚动条宽度
                 * autohidemode   滚动条是否隐藏
                 * background     滚动条背景颜色
                 * preventmultitouchscrolling    防止多触点事件引发滚动
                 */
                $(".c_approval_box").getNiceScroll().remove();
                $(".c_approval_box").niceScroll(".c_approval_content", {
                    cursorcolor: "#2f78ef",
                    cursorwidth: "5px",
                    autohidemode: false,
                    background: "#e6e6e6",
                    preventmultitouchscrolling: true,
                    bouncescroll: false
                });
            }
        },
        updated() {
            console.log('更新了');
            // this.scrollBar();
        },
        beforeDestroy() {
            this.$zero.eventBus.$off("addBookLoadMore");
            this.$zero.eventBus.$off("departmentItem");
            // this.$zero.removeLocalStorageItem('getLocalStorageItem')
            console.log(this.isDel);

            if (this.isDel) {
                this.$zero.removeSessionStorage('ruleSettingsData')
                this.$zero.removeSessionStorage('ruleTemplate')
            }
        },
        components: {
            MaskLayer //注册遮罩层组件
        }
    }
</script>

<style>
    .procedure_set_rule {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .procedure_set_rule .c_rule_header {
        position: relative;
        width: 100%;
        height: 51px;
    }

    .procedure_set_rule .c_rule_header_btn_one {
        padding: 6px 20px;
        border-radius: 0px;
        position: absolute;
        right: 135px;
        top: 12px;
    }

    .procedure_set_rule .c_rule_header_btn_two {
        padding: 6px 27px;
        border-radius: 0px;
        background-color: #2f78ef;
        border-color: #2f78ef;
        position: absolute;
        right: 40px;
        top: 12px;
    }

    .c_sule_settings {
        flex: 1;
        padding: 20px 40px 30px;
        box-sizing: border-box;
        position: relative;
        overflow: hidden;
    }

    /* 审批模块 */
    .c_approval_box {

        /* padding: 30px 40px 10px; */
        background-color: #fff;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        /* position: relative; */
        /* display: none; */
        overflow: auto;
    }

    .c_approval_content {
        padding: 30px 40px 0px;
        box-sizing: border-box;
        width: 100%;
        /* height: 100%; */
        /* overflow: auto; */
    }


    .c_visible_range {
        width: 100%;
        box-sizing: border-box;
        padding-bottom: 30px;
        border-bottom: 1px solid #e6e6e6;
    }

    /* 标题 */
    .c_visible_range_title,
    .c_approval_process_title {
        font-weight: bold;
        /* color: #333; */
        margin-bottom: 20px;
    }

    .c_approval_process_title {
        padding-top: 30px;
    }

    /* 内容 */
    .c_visible_range_range {
        display: flex;
        width: 100%;
        flex-wrap: wrap;
    }

    .c_visible_range_range_branch {
        background-color: #2f78ef;
        color: #fff;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 30px;
        line-height: 30px;
        /* width: 140px; */
        padding: 0 15px;
        margin-right: 10px;
        margin-bottom: 10px;
    }

    .c_visible_range_range_branch>span {
        margin-left: 5px;
    }

    .c_visible_range_range_btn {
        font-size: 14px;
        color: #2f78ef;
        height: 30px;
        line-height: 30px;
        cursor: pointer;
        margin-left: 8px;
    }

    .c_approval_process_box {
        display: flex;
        /* align-items: center; */
        margin-bottom: 20px;
    }

    .c_approval_process_box .c_title,
    .c_condition_box .c_title,
    .c_revoke_box .c_title {
        color: #999;
        width: 100px;
        font-size: 14px;
        padding-top: 15px;
    }

    .c_condition_box .c_title,
    .c_revoke_box .c_title {
        padding-top: 0px;
    }

    .c_approval_by_list {
        display: flex;
    }

    .c_approval_by_name {
        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        align-items: center;
    }

    .c_approval_by_name>img {
